.header-home {
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
  width: 100%;
  background-color: black;
  position: fixed;
}

.header-home a {
  text-decoration: none;
  font-size: 20px;
  width: 85px;
  margin: 20px;
  border: 0px solid black;
  color: #00ff03;
  transition: border-bottom .3s, width .3s;
}

.header-home a:hover {
  cursor: pointer;
  border-bottom: 1px solid #00ff03;
}

.header-home .selected {
  border-bottom: 1px solid #00ff03;
}

